import React from 'react'
import pic from '../../assets/pic.jpg'
import './Main.scss'

export default function Main() {
    return (
        <div className='main'>

            <img className='main__pic' src={pic}></img>

            <h1 className="main__name">
                Christopher Fotos
            </h1>
            <p className="main__tagline">
                    Every project is a passion project.
            </p>
        </div>
    )
}
