import React from 'react'
import './Contact.scss'
import github from '../../assets/gh.png'
import linkedin from '../../assets/in.svg'

export default function Contact() {
    return (
        <div className='contact'>
            <h1 className="contact__heading">
                Drop me a line
            </h1>

            <p className="contact__text">
                <p> Wanna talk about my projects? Wanna work with me? I'd love to hear from you! </p>

                <p className="contact__label"> Email: </p>
                ChrisFotosMusic@gmail.com


                <div className="contact__wrap">
                <a href="https://github.com/ChristopherFotos" className="contact__link">
                    <img src={github} alt="" className="contact__img"/>
                    <p className="contact__label">github</p>
                </a>
                </div>

                <div className="contact__wrap">
                <a href="https://www.linkedin.com/in/christopherfotos/" className="contact__link">
                    <img src={linkedin} alt="" className="contact__img"/>
                    <p className="contact__label">Linkedin</p>
                </a>
                </div>
                
            </p>
        </div>
    )
}
