import React from 'react'
import gh from '../../assets/gh.png'
import npmpic from '../../assets/npm.svg'
import './Project.scss'

export default function Project(props) {
    const { name, text, img, github, link, npm } = props.project
    const { index } = props; 

    return (
        <div className="project__border">
            <div className='project'>
                <h3 className="project__title">{name}</h3>
                <div className= "project__content">
                    <img src={img} alt="" className="project__img"/>
                    <div className="project__inner">
                    <p className="project__text">
                        {text}
                    </p>
                    <div className="project__icon-wrap">
                        <div className="project__icon-inner">

                        <a href={github} target='blank' className='project__link'>
                            <img src={gh} alt="" className="project__icon" />
                        </a>
                        
                        {link !== '' && <a href={link} className='project__link'>try it out</a>}
                        
                        {npm  &&
                            <a href={npm} target = 'blank' className='project__link'>
                                <img src={npmpic} alt="" className="project__npm"/>
                            </a>
                        }


                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
